*{
  margin:0px;
  padding:0px;
  border-radius: 10px;
}



.App {
  text-align: center;
  color: #4ECDC4; 
}

#title{
  font-family: 'Permanent Marker', cursive;
  color: #4ecDc4;
  text-shadow: 2px 10px gray;
  top: 0;
  left: 25%;
  position: fixed;
  z-index: 10;
  text-align: center;
  background: white;
}



#landingContainer {
  display: block;
  width: 100%;
  
  padding: 10% 0% 10% 0%;
  margin: 5% 0% 5% 0%;
  border-bottom: .5px dotted gray;
  font-size: x-large;
  
}

svg {
  border:1px solid #4ECDC4;
}

span{
  background: none;
  border: none;
  font-size: 2em;
  opacity: .9;
  color: #FF6B6B;
}

span:hover{
  font-size: 2.5em;
  color: #FFE66D;
  text-shadow: 1px 1px 10px black;
  opacity: 1;
}

#alphabet{
  display: flex;
  align-content: space-around;
  justify-content: center;
  align-content: bottom;
  /* border-top: 1px solid rgba(255, 255, 255, 0.246);
  border-bottom: 1px solid rgba(255, 255, 255, 0.246); */
}

#container{
  padding: 5%;
  display: grid;
  grid-template-columns: 55% 45%;
  grid-template-rows: 60% 40%;
  grid-gap: 1%;
}

button{
  grid-column-start: 1;
  grid-row-start: 2;
  grid-row-start: 3;
  background: #4ECDC4;
  border: none;
  padding: 1%;
  color: white;
  cursor: pointer;
  outline: none;
  font-size: .8em;
  margin: 0;
  box-shadow: 10px 10px gray;
}

#word{
  padding: 1%;
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: 4;
  font-size: 1em;
  background: lightblue;
  box-shadow: 10px 10px gray;
  color: white;
  
}